'use strict';

// MODULES //

import copy from '@stdlib/utils-copy';
import data from './../data/data.js';


// MAIN //

/**
* Returns the text of Moby Dick.
*
* @returns {ObjectArray} data
*
* @example
* var data = text();
* // returns [ {...}, {...}, ... ]
*/
function text() {
	return copy( data );
}


// EXPORTS //

export default text;
